
.chat{
    flex:0.65;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;

}
.chat__header{
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
    font-size: 15px;
}

.chat__name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}



.chat__timestamp{
    margin-left: 10px;
    font-size: xx-small;
}

.chat__headerInfo{
    flex: 1;
    padding-left: 20px;
}

.chat__headerInfo > h3{
    margin-bottom: 3px;
    font-weight: 500;
}
.chat__headerInfo > p{
    font-size: 11px;
}

.chat__headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat__body{
    flex: 1;
  background-image:url("./wp4410716-chat-wallpapers.jpg");
  background-repeat: repeat;
  background-position: center;
  padding: 30px;
  overflow-y: scroll;
}
.chat__body::-webkit-scrollbar { width: 0 !important }

.chat__message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    background-color: white;
    width: fit-content;
    border-radius: 10px;
    margin-bottom: 15px;
}
.chat__receiver{
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    border-top: 1px solid lightgray;
}

.chat__footer >form{
    flex: 1;
    display: flex;
}
.chat__footer >form>input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border:none ;
}

.chat__footer>form>button{
    display: none;
}

.chat__footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}