.login{
    background-color: #f8f8f8;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;

}

.login__container{
    padding: 100px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 1px 3px rgb(54, 51, 51),0 1px ;
}

.login__container > img{
    object-fit: contain;
    height: 100px;
    margin-bottom: 40px;
}

.login__container >button{
    margin-top: 50px;
    text-transform: inherit !important;
    background-color: #0a8d48;
    color: white;
}